import {useEffect, useState} from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Button, PanelHeaderBack, Div, CardGrid, Card, Cell} from '@vkontakte/vkui';




const about = props => {



	const [todos, setTodos] = useState([])

	useEffect(() =>{
		
		const fetchData = async() =>{
		try{

			const response = await fetch('https://jsonplaceholder.typicode.com/todos')
			const data = await response.json()

			setTodos(data)

		}catch(error){
			console.log(error)

		}
		
		}

		fetchData()

	}, [])



	return(
		<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
            Тутоирал
		</PanelHeader>
		
		<Div> 
		<h1> {props.lol}</h1>
		<CardGrid size="l">

		{todos.map((todo) =>{
        return (
          <Card onClick={props.go} data-to="pageS" className="parent" mode="outline"> 
		  	<div style={{ height: 64 }} />
		    <Button className='child' size="s">{todo.title}</Button>
		  </Card>
        )
      	})}
          <Card onClick={props.go} data-to="pageS" className="parent" mode="outline">
            <div style={{ height: 64 }} />
			<Button className='child' size="s"> ты же хочешь нажать?</Button>
			
          </Card>

          <Card onClick={props.go} data-to="pageS" className="parent" mode="outline">
            <div style={{ height: 72 }} />
			<Button className='child' size="s"> Нет?</Button>
            <Cell> ДА ну?</Cell>
			
          </Card>

          <Card onClick={props.go} data-to="pageS" className="parent" mode="outline">
            <div style={{ height: 72 }} />
			<Button className='child' size="s"> ок айм го</Button>
			
          </Card>
        </CardGrid>
		</Div>
		
	</Panel>

	);


}

about.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	lol: PropTypes.string.isRequired,
};

export default about;